}
.tripInfo {
    display: inline;
}
.popup-background {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #000000c2;
    display: flex;
    display: webkit-flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
}

.popup-container {
    z-index: 9999;
    text-align: center;
}

.popup {
    padding-left: 24px;
    padding-right: 0px;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: white;
    /* display: inline-block; */
    text-align: left;
    border-radius: 8px;
    /* max-height: 50vh; */
    max-width: 80vw;
    margin: auto;
    width: auto;
}

@media screen and (min-width: 700px) {
    .popup {
        max-width: 50vw;
        width: auto;
    }
}

.popup-header {
    margin-right: 24px;
}

.popup-header h3 {
    margin: 0;
    padding: 0;
}

.popup-contents {
    margin-right: 24px;
    overflow-y: auto;
    /* max-height: -webkit-fill-available; */
    max-height: 50vh;
    /* max-width: 70vw; */
    overflow-wrap: break-word;
    padding-right: 24px;
    height: 100%;
}

.popup-actions-container {
    margin-right: 8px;
    text-align: right;
    padding-right: 14px;
    padding-left: 14px;
    padding-top: 14px;
}

.popup-action-button {
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 8px;
    padding-bottom: 8px;
    display: inline-block;
    background: none;
    border-radius: 5px;
    border-width: 1px;
    border-color: gray;
    border-style: solid;
}
.loader {
    position: relative;
    display: inline-grid;
    grid-template-columns: 33% 33% 33%;
    grid-gap: 2px;
    width: 50px;
    height: 50px;
    vertical-align: middle;
}
.loader > div {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    background: tomato;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-animation: loader 2s infinite linear;
            animation: loader 2s infinite linear;
}
.loader > div:nth-of-type(1), .loader > div:nth-of-type(5), .loader > div:nth-of-type(9) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.loader > div:nth-of-type(4), .loader > div:nth-of-type(8) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loader > div:nth-of-type(2), .loader > div:nth-of-type(6) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.loader > div:nth-of-type(3) {
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}

@-webkit-keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@keyframes loader {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

.loading-sign {
    display: inline-block;
    width: auto;
    padding: 0;
    vertical-align: middle;
    margin: 0px 0px 0px 30px;
}
